.box{
    display: flex;
    padding: 5px;
    border: 1px solid #cccccc;
    min-height: 100px;
    flex-wrap: wrap;
    box-shadow: 0 2px 3px #ccc;
}

.boxDetails{
    width: 16%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid #ccc;
    padding: 10px 2px;
    border-radius: 10px;
    height: 40px;
    margin-top: 7px;
    margin-left: 4px;
}
.boxDetails  i:hover{
    color: red;
    cursor: pointer;
}
.addFile{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-flow: column;
}
.inputFile{
    display: flex;
    flex-flow: column;
}
input[type="file"]{
    display: none;
}
.addImage{
    background-color: rgb(47, 178, 187);
    padding:5px 10px;
    border-radius: 10px;
    color: white;
    font-size: 15px;
}
.inputFile{
    padding: 50px;
    border: 4px dashed #ddd;
    margin-top: 10px;
    font-size: 20px;
}
.centercontain{
    display: flex;
    align-items: center;
}
.imageBrand{
    width: 200px;
    margin: 7px;
}
.mediasection img{
    width: 150px;
    height: auto;
}
.btnAddSurvey{
    display: flex;
    justify-content: center;
    align-items: center;
}
.removeSurvey{
    display: flex;
    align-items: center;
}
.clickShowSurvey{
    text-decoration: underline;
    cursor: pointer;
    color: blue;
}